.property {
    background: #0B1A25;
    padding-top: px_to_vw_desktop(300);
    padding-bottom: px_to_vw_desktop(220);
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;
    @include mobileMax952p{
        padding: 100px 16px 84px 16px;
    }
    p {
        @include mulish500();
        font-size: 16px;
        line-height: 1.5em;
        text-align: center;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: #B58C67;

        @include windowMax1700p {
            font-size: 14px;
        }
        @include windowMax1280p {
            font-size: 12px;
        }
        @include mobileMax952p{
            font-size: 12px;
        }
    }

    h1 {
        @include tenor();
        max-width: px_to_vw_desktop(915);
        font-size: px_to_vw_desktop(64);
        padding-top: px_to_vw_desktop(40);
        line-height: 1.15625em;
        text-align: center;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        color: #B58C67;
        @include mobileMax952p{
            font-size: 40px;   
            max-width: unset;
            padding-top: 24px;
        }
    }
}