.quote{
    padding: px_to_vw_desktop(310) px_to_vw_desktop(276);
    margin: 0;
    display: flex;
    justify-content: space-between;
    width: auto;
    position: relative;
    z-index: 1;
    background-color: white;
    @include mobileMax952p{
        padding: 100px 0;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        max-width: 450px;
        margin-left: auto;
        margin-right: auto;
    }
    &__text{
        max-width: px_to_vw_desktop(672);
        position: relative;
        display: flex;
        flex-direction: column;
        @include mobileMax952p{
            max-width: 450px;
            width: 100%;
            align-items: center
        }
        p{
            @include mulish500();
            font-size: 16px;
            line-height: 1.5em;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #B58C67;
            @include windowMax1700p{
                font-size: 14px;
            }
            @include windowMax1280p{
                font-size: 12px;
            }
            @include mobileMax952p{
                font-size: 12px;
            }
        }
        h1{
            @include tenor();
            font-size: px_to_vw_desktop(32);
            line-height: 1.5em;
            letter-spacing: -0.04em;
            text-transform: uppercase;
            color: #B58C67;
            margin-top: px_to_vw_desktop(30);
            @include mobileMax952p{
                font-size: 24px;
                text-align: center;
                padding: 24px 0;
                margin: 0;
            }
        }
        img{
            width: px_to_vw_desktop(36);
            height: px_to_vw_desktop(25);
            margin-top: px_to_vw_desktop(80);
            @include mobileMax952p{
                order: -1;
                width: 26px;
                height: 17px;
                padding-bottom: 16px;
                margin-top: 48px;
            }
        }
        &__bottom{
            position: absolute;
            bottom: 0;
            @include mobileMax952p{
                position: relative;
            }
        }
    }
    &__picture{
        img{
            height: px_to_vw_desktop(664);
            aspect-ratio: 447/664;
            @include mobileMax952p{
                height: auto;
                width: 100%;
            }
        }
        @include mobileMax952p{
            width: 100%;
        }
    }
}