.residental{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #0B1A25;
    position: relative;
    z-index: 1;
    &__rectangle{
        width: px_to_vw_desktop(174);
        border-top: 1px solid #908F8B33;
        @include mobileMax952p{
            width: 134px;
        }
    }
    p{
        @include tenor();
        font-size: px_to_vw_desktop(32);
        line-height: 1.25em;
        text-align: center;
        letter-spacing: -0.04em;
        text-transform: uppercase;
        color: #B58C67;
        padding-top: px_to_vw_desktop(38);
        @include mobileMax952p{
            font-size: 32px;
            padding-top: 24px;
        }
    }
    &__content{
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        padding: 0 px_to_vw_desktop(204);
        padding-top: px_to_vw_desktop(140);
        padding-bottom: px_to_vw_desktop(92);
        gap: px_to_vw_desktop(102) 0;
        width: 100%;
        @include mobileMax952p{
            padding: 48px 16px 78px 16px;
            padding-top: 48px;
            flex-direction: column;
            max-width: 482px;
            gap: 68px;
        }
        &__item{
            width: 50%;
            padding: 0 px_to_vw_desktop(82);
            box-sizing: border-box;
            @include mobileMax952p{
                padding: 0;
                width: 100%;
            }
            img{
                width: 100%;
            }
            h1{
                @include tenor();
                font-size: 24px;
                line-height: 1.333333em;
                letter-spacing: -0.04em;
                text-transform: uppercase;
                color: #B58C67;
                border-top: solid 1px #908F8B33;
                padding-top: 1em;
                margin-top: 1.33333em;
                @include windowMax1700p{
                    font-size: 20px;
                } 
                @include windowMax1280p{
                    font-size: 18px;
                }
                @include mobileMax952p{
                    font-size: 20px;
                }
            } 
            &__text{
                p{
                    @include mulish500();
                    font-size: 14px;
                    line-height: 2em;
                    letter-spacing: 0.08em;
                    text-transform: uppercase;
                    color: #6F6862;
                    padding-top: 4px;
                    text-align: start;
                    @include windowMax1700p{
                        font-size: 12px;
                    } 
                    @include windowMax1280p{
                        font-size: 10px;
                    }
                    @include mobileMax952p{
                        font-size: 12px;
                        padding-top: 0px;
                    }
                }
            }
        }
    }
    &__arrow{
        padding-bottom: px_to_vw_desktop(220);
        @include mobileMax952p{
            padding-bottom: 110px;
        }
        button{
            width: px_to_vw_desktop(120);
            height: px_to_vw_desktop(120);
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #B58C67;
            transition: 0.3s background-color;
            cursor: pointer;
            @include mobileMax952p{
                width: vw_mobile(82, 999);
                max-width: 82px;
                height: vw_mobile(82, 999);
                max-height: 82px;
            }
            &:hover{
                background-color: #B58C67;
            }
            img{
                width: px_to_vw_desktop(48);
                height: px_to_vw_desktop(48);
                @include mobileMax952p{
                    width: vw_mobile(32,999);
                    height: vw_mobile(32,999);
                    max-width: 32px;
                    max-height: 32px;
                }
            }
        }   
    }
}