.carousel{
    margin-top: px_to_vw_desktop(195);
    display: grid;
    grid-template-columns: 2fr 1fr;
    height: px_to_vw_desktop(780);
    overflow: hidden;
    @include mobileMax952p{
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 100px;
        max-width: 450px;
        margin-left: auto;
        margin-right: auto;
    }
    .hidden{
        opacity: 0;
        pointer-events: none;
    }
    .scaled{
        scale: 0.8;
        @include mobileMax952p{
            scale: unset;
        }
    }
    &__images{
        position: relative;
        width: 100%;
        @include mobileMax952p{
            height: auto;
            aspect-ratio:  343/446;
        }
        &__block{
            position: absolute;
            margin-top: -48px;
            top: px_to_vw_desktop(248);
            left: 0;
            height: px_to_vw_desktop(416);
            width: px_to_vw_desktop(324);
            @include windowMax1700p{
                margin-top: -45px;
            }
            @include windowMax1280p{
                margin-top: -41px;
            }
            @include mobileMax952p{
                width: 100%;
                height: auto;
                aspect-ratio:  343/446;
                margin-top: unset;
                top: 0;
                opacity: 0;
            }
            p{
                @include mulish500();
                text-transform: uppercase;
                color: #B58C67;
                font-size: 16px;
                line-height: 1.5em;
                letter-spacing: 0.08em;
                @include windowMax1700p{
                    font-size: 14px;
                }
                @include windowMax1280p{
                    font-size: 12px;
                }
                @include mobileMax952p{
                    display: none;
                }
            }
            span{
                @include tenor();
                font-size: 24px;
                line-height: 1.33333em;
                letter-spacing: -0.04em;
                @include windowMax1700p{
                    font-size: 19px;
                }
                @include windowMax1280p{
                    font-size: 14px;
                }
            }
            img{
                margin: 24px 0 16px 0;
                width: 100%;
                @include mobileMax952p{
                    margin: unset;
                }
            }
            &-transition{
                transition: 1s;
            }
        }
        .active{
            opacity: 1;
            @include mobileMax952p{
                opacity: 1 !important;
            }
        }
        .right{
            left: 100%;
            top:   0;
            width: px_to_vw_desktop(600);
            transform: translateX(-100%);
            p{
                opacity: 0;
                transition: 0.8s ease-out;
            }
            @include mobileMax952p{
                z-index: 3;
                width: 100%;
                height: auto;
                aspect-ratio:  324/416;
                transform: unset;
                top: unset;
                left: unset;
                opacity: 0;
            }
        }
        .moving{
            z-index: 2;
        }
        display: flex;
    }
    &__description{
        position: relative;
        display: flex;
        flex-direction: column;
        padding-top: px_to_vw_desktop(200);
        margin-left: px_to_vw_desktop(150);
        margin-right: px_to_vw_desktop(88);
        @include mobileMax952p{
            margin: 0;
            padding-top: 15px;
            margin-bottom: 32px;
        }
        p{
            @include mulish400();
            font-size: 16px;
            line-height: 1.5em;
            letter-spacing: 0.02em;
            color: #3E3025;
            @include windowMax1700p{
                font-size: 14px;
            }
            @include windowMax1280p{
                font-size: 12px;
            }
        }
        span{
            @include mulish500();
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #B58C67;
        }
        h1{
            @include tenor();
            font-size: 32px;
            line-height: 1.25em;
            letter-spacing: -0.04em;
            text-transform: uppercase;
            color: #B58C67;
            padding-top: 1em;
            padding-bottom: 0.5em;
            @include windowMax1700p{
                font-size: 26px;
            }
            @include windowMax1280p{
                font-size: 19px;
            }
            @include mobileMax952p{
                font-size: 24px;
                line-height: 30px;
            }
        }
        &__text{
            position: relative;
            &__block{
                position: relative;
                transition: opacity 0.3s ease-out;
                &-hidden{
                    position: absolute;
                    top: 24px;
                    @include windowMax1700p{
                        top: 21px;
                    }
                    @include windowMax1280p{
                        top: 18px;
                    }
                }
            }
        }
        &__controls{
            position: absolute;
            bottom: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include mobileMax952p{
                position: unset;
            }
            &__elements{
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 100%;
                p{
                    @include tenor();
                    font-size: 32px;
                    min-width: 60px;
                    line-height: 1em;
                    color: #B58C67;
                    @include windowMax1700p{
                        font-size: 24px;
                        min-width: 42px;
                    }
                    @include windowMax1280p{
                        font-size: 16px;
                        min-width: 28px;
                    }
                    @include mobileMax952p{
                        font-size: 12px;
                        min-width: 21px;
                    }
                }
                &__wrap{
                    display: flex;
                    gap: 16px;
                    &__text{
                        display: none;
                        @include mobileMax952p{
                            display: block;
                            font-size: 12px;
                            line-height: 1em;
                            color: #B58C67;
                        }
                    }
                    &__numbers{
                        display: flex;
                        &__block{
                            position: absolute;
                            &-active{
                                position: relative;
                            }
                        }
                    }
                }
                &__arrows{
                    padding-left: 24px;
                    button{
                        cursor: pointer;
                    }
                    .arrow-img{
                        width: 56px;
                        height: 56px;
                        @include windowMax1700p{
                            width: 52px;
                            height: 52px;
                        }
                        @include windowMax1280p{
                            width: 48px;
                            height: 48px;
                        }
                        @include mobileMax952p{
                            width: 48px;
                            height: 48px;
                        }
                        path{
                            transition: 0.3s;
                        }
                        &:hover{
                            path{
                                stroke: #3E3025;
                            }
                        }
                    }
                }
            }   
            &__statusline{
                position: relative;
                width: calc(100% - 14.5px);
                @include windowMax1700p{
                    width: calc(100% - 14px);
                }
                @include windowMax1280p{
                    width: calc(100% - 12.5px);
                }
                @include mobileMax952p{
                    width: calc(100% - 12.5px);
                    position: absolute;
                    bottom: -32px;
                }
                height: 1px;
                background-color: #DCD4CC;
                &__line{
                    position: absolute;
                    background-color: #B58C67;
                    height: 1px;
                    z-index: 2;
                }
            }
        }
    }
}