.hero {
    height: 100vh;
    height: var(--vh);
    height: 100svh;
    width: 100%;
    max-width: 100vw;
    position: relative;
    overflow: hidden;
    &__line-wrap{
        overflow: hidden;
    }
    &__line{
        opacity: 0;
        transform: translateY(100%);
    }

    &__carousel {
        height: 100%;
        width: 100%;
        position: absolute;
        z-index: 1;

        &__filter {
            height: 100%;
            width: 100%;
            background: linear-gradient(0deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.186584) 32.15%, rgba(0, 0, 0, 0) 70.77%), rgba(0, 0, 0, 0.4);
            background-blend-mode: multiply;
            position: absolute;
            z-index: 2;
        }
        &__wrap{
            display: flex;
            width: 400vw;
            transition: 1.5s ease-out;
        }
        img {
            position: relative;
            height: var(--vh);
            height: 100svh;
            width: 100%;
            z-index: 1;
            object-fit: cover;
        }
    }

    &__content {
        height: 100%;
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        p {
            @include mulish500();
            font-size: 16px;
            line-height: 36px;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #F5F3F1;
            @include  windowMax1700p{
                font-size: 14px;
                line-height: 34px;
            }
            @include windowMax1280p {
                font-size: 12px;
                line-height: 32px;
            }
            @include mobileMax952p{
                font-size: 12px;
                line-height: 32px;
            }
        }

        h1 {
            @include tenor();
            font-size: px_to_vw_desktop(120);
            line-height: 1.15em;
            letter-spacing: -0.02em;
            text-transform: uppercase;
            color: #F5F3F1;
            @include mobileMax952p{
                font-size: vw_mobile(47, 120);
            }
        }
        .line-wrapper{
            overflow: hidden;
        }
        .hidden{
            display: none;
        }
        &__text {
            gap: 16px;
            p{
                transform: translateY(-150%);
                opacity: 0;
            }
        }

        &__controls {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-top: px_to_vw_desktop(40);
            padding: 16px 0 16px 0;
            border-top: 1px solid #908F8B1E;
            transform: translateY(100%);
            &__description {
                display: flex;
                gap: 32px;
                @include mobileMax952p{
                    gap: vw_mobile(16,1920);
                }
            }

            &__arrows {
                display: flex;
                img{
                    width: px_to_vw_desktop(56);
                    height: px_to_vw_desktop(56);
                    @include mobileMax952p{
                        width: 48px;
                        height: 48px;
                    }
                }
                button{
                    cursor: pointer;
                }
            }
        }
    }
}