.nav {
    position: absolute;
    height: 92px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    z-index: 9999;
    transform: translateY(-100%);
    &__logo{
        &__img{
            width: px_to_vw_desktop(138);
            height: px_to_vw_desktop(42);
            transition: filter 1s ease-in-out;
            @include mobileMax952p{
                width: 98px;
                height: 30px;   
            }
            &-active{
                filter: invert(1);
            }
        }  
    };
    &__burger{
        pointer-events: all;
        @include mobileMax952p{
            cursor: pointer;
            &::before{
                content: "";
                height: 2px;
                background-color: white;
                width: 18px;
                display: block;
                transition: 0.3s ease-in-out rotate, 1s ease-in-out background-color;
                transform-origin: 5px;
            }
            &::after{
                content: "";
                height: 2px;
                background-color: white;
                width: 18px;
                margin-top: 4px;
                display: block;
                transition: 0.3s ease-in-out rotate, 1s ease-in-out background-color;
                transform-origin: 5px;
            }
        }
    }
    &__burger-active{
        @include mobileMax952p{
            &::before{
                rotate: 45deg;
                background-color: #3E3025;
            }
            &::after{
                rotate: -45deg;
                background-color: #3E3025;
            }
        }
    }

    &__menu {
        display: flex;
        align-items: center;
        gap: 48px;
        @include mobileMax952p{
            display: none;
        }
        &__button{
            padding: 5px 28px 5px 28px;
            border: 1px solid #F5F3F1;
            border-radius: 20px;
            transition: 0.3s ease-out;
            &:hover{
                color: #3E3025;
                background-color: #F5F3F1;
            }
        }
        a {
            @include mulish500;
            font-size: 16px;
            line-height: 1.875em;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #F5F3F1;
            @include windowMax1700p{
                font-size: 14px;
            }
            @include windowMax1280p{
                font-size: 12px;
            }
        }
        
    }

    &__menuMobile{
        display: none;
        position: absolute;
        padding: 118px 16px 45px 16px;
        left: -16px;
        top: calc(-1 * var(--vh));
        background-color: #F5F3F1;;
        z-index: -1;
        width: 100vw;
        height: var(--vh);
        height: 100svh;
        transition: 0.75s ease-out;
        box-sizing: border-box;
        flex-direction: column;
        justify-content: space-between;
        @include mobileMax952p{
            display: flex;  
        }
        &__menu{
            display: flex;
            flex-direction: column;
            gap: 32px;
            a{
                @include tenor();
                font-size: 32px;
                line-height: 1.1875em;
                text-transform: uppercase;
                color: #B58C67;
            }
        }
        &__text{
            display: flex;
            justify-content: space-between;
            padding-top: 24px;
            border-top: 1px solid #908F8B33;
            h1{
                @include mulish500();
                font-size: 14px;
                line-height: 2.142857em;
                letter-spacing: 0.08em;
                text-transform: uppercase;
                color: #B58C67;
                padding-bottom: 20px;
            }
            p, a{
                @include mulish400();
                font-size: 14px;
                line-height: 1.714285em;
                color: #3E3025;
            }
            &__company{
                display: flex;
                flex-direction: column;
            }
            &__contact{
                display: flex;
                flex-direction: column;
            }
        }
    }

    &__menuMobile-active{
        @include mobileMax952p{
            top: 0 !important;
        }
    }
}