.footer{
    display: flex;
    flex-direction: column;
    padding-top: px_to_vw_desktop(190);
    @include mobileMax952p{
        flex-direction: column;
        padding-top: 100px;
    }
    &__content{
        display: flex;
        justify-content: space-between;
        @include mobileMax952p{
            flex-direction: column;
        }
        &__menu{
            display: flex;
            flex-direction: column;
            gap: px_to_vw_desktop(56);
            @include mobileMax952p{
                gap: 24px;
            }
            a{
                @include tenor();
                font-size: px_to_vw_desktop(48);
                line-height: 1em;
                text-transform: uppercase;
                color: #B58C67;
                @include mobileMax952p{
                    font-size: 32px;
                }
            }
        }
        &__text{
            display: flex;
            gap: px_to_vw_desktop(180);
            @include mobileMax952p{
                gap: 0px;
                justify-content: space-between;
                padding-top: 64px;
            }
            h1{
                @include mulish500();
                font-size: 16px;
                line-height: 1.875em;;
                letter-spacing: 0.08em;
                text-transform: uppercase;
                color: #B58C67;
                padding-bottom: 16px;
                @include windowMax1700p{
                    font-size: 14px;
                }
                @include windowMax1280p{
                    font-size: 12px;
                }
                @include mobileMax952p{
                    font-size: 14px;
                }
            }
            p{
                @include mulish400();
                font-size: 16px;
                line-height: 1.875em;;
                color: #3E3025;
                @include windowMax1700p{
                    font-size: 14px;
                }
                @include windowMax1280p{
                    font-size: 12px;
                }
                @include mobileMax952p{
                    font-size: 14px;
                    line-height: 24px;
                    padding-top: 0px;
                }
            }
            a{
                @include mulish400();
                font-size: 16px;
                line-height: 2em;
                color: #3E3025;
                @include windowMax1700p{
                    font-size: 14px;
                }
                @include windowMax1280p{
                    font-size: 12px;
                }
                @include mobileMax952p{
                    line-height: 24px;
                    font-size: 14px;
                }
            }
            &__contact{
                display: flex;
                flex-direction: column;
            }
            &__company{
                display: flex;
                flex-direction: column;
                padding-right: 48px;
                @include mobileMax952p{
                    padding-right: 0px;
                }
            }
        }
    }
    &__holding{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: px_to_vw_desktop(260);
        padding-bottom: px_to_vw_desktop(40);
        @include mobileMax952p{
            flex-direction: column;
            align-items: flex-start;
        }
        p{
            @include mulish500();
            font-size: 16px;
            line-height: 1.875em;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #B58C67;
            @include windowMax1700p{
                font-size: 14px;
            }
            @include windowMax1280p{
                font-size: 12px;
            }
            @include mobileMax952p{
                @include mulish400();
                letter-spacing: 0.04em;
                font-size: 12px;
                text-transform: none;
            }
        }
        &__wrap{
            @include mobileMax952p{
                position: relative;
                overflow: hidden;
                width: 100vw;
                height: 26px;
                transform: translate3d(0,0,0);
                margin-left: -16px;
                margin-top: 20px;
                margin-bottom: 32px;
            }
            &__logos{
                position: relative;
                width: px_to_vw_desktop(904);
                height: px_to_vw_desktop(26);
                background-size: contain;
                display: flex;
                img{ 
                    height: inherit;
                    @include mobileMax952p{
                        padding-right: 12px;
                    }
                }
                &-mobile{
                    display: none;
                    @include mobileMax952p{
                        display: block;
                        padding-left: 12px;
                    }
                }
                @include mobileMax952p{
                    width: 1808px;
                    height: 26px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    transform: translate3d(0, 0, 0);
                    animation: moveSlideshow 12s linear infinite;
                }
            }
        }
        
    }
    &__bottom{
        display: flex;
        justify-content: space-between;
        padding-top: px_to_vw_desktop(40);
        padding-bottom: px_to_vw_desktop(48);
        border-top: 1px solid #908F8B33;
        @include mobileMax952p{
            padding-top: 22px;
            padding-bottom: 27px;
        }
        img{
            width: 44px;
            @include mobileMax952p{
                width: 32px;
            }
        }
        p{
            @include mulish500();
            font-size: 12px;
            line-height: 30px;
            text-align: center;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            @include mobileMax952p{
                font-size: 8px;
            }
        }
    }
}

@keyframes moveSlideshow {
    100% { 
      transform: translateX(calc(-50% - 24px));  
    }
}